.hero-img {
    height: 60.6vh;
    width: 100%;
    position: relative;
    background: rgba(0, 0, 0, .5);
}

.hero-img::before {
    content: "";
    background: url("https://images.unsplash.com/photo-1510915228340-29c85a43dcfe?q=80&w=2070&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D");
    height: 100%;
    width: 100%;
    background-size: cover;
    background-position: center top;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.heading {
    display: flex;
    height: 100%;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


.hero-img h1 {
    font-size: 2.4rem;
}

.hero-img p {
    font-size: 1.4rem;
    text-align: center;
}

@media screen and (max-width:640px) {
    .hero-img h1 {
        font-size: 2rem;
    }
}